@font-face {
    font-family: 'Name';
    src: url('https://www.dafont.com/rosmatika.font');
}

.homepage-section {
    background-size: cover;
    display: block;
    font-family: Andale Mono, monospace;
    align-items: center;
    /* backdrop-filter: blur(4px); */
}

.home-section {   
    color: #914b04;
    text-align: left;
    margin-top: 6rem;
    padding: 2rem 3.5rem;      
    
}

.home-section > h3 {
    font-size: 2.5rem;
}

.name {
    font-size: 3.5rem;
    font-family: helvetica, sans-serif;
    /* font-family: 'Name'; */
}


@media screen and (max-width: 768px) {

    .homepage-section {
        display: flex;
        margin: auto;
        backdrop-filter: blur(0px);
        text-align: center;

    }

    .home-section {
        text-align: center;
        font-size: 1.2rem;
        margin-top: 20rem;
    }

    .time {
        text-align: center;
        font-size: 3rem;
        text-shadow: 2px 2px 6px black;
    }

}

/* Light mode */

.homepage-section-light {
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: Andale Mono, monospace;
    align-items: center;
    backdrop-filter: blur(4px);
    

}

.home-section-light {   
    color: white;
    text-align: center;
    margin-top: 2rem;
    padding: 2rem 3.5rem;
    font-size: 1.5rem;
}

.time-light {
    padding: 0 0 2rem 2rem;
    font-family: 'digital dream';
    font-size: 5rem;
    color: rgb(49, 235, 179);

}

@media screen and (max-width: 768px) {

    .homepage-section-light {
        display: flex;
        margin: auto;
        backdrop-filter: blur(0px);
        text-align: center;

    }

    .home-section-light {
        text-align: center;
        font-size: 1rem;
        margin-top: 20rem;
    }

    .time-light {
        text-align: center;
        font-size: 3rem;
    }

}

